import { useEffect } from "react";
import { useLocation } from "react-router-dom";

export default function ScrollToTop(props) {
const { pathname } = useLocation();

useEffect(() => {
window.scrollTo(0, 0);
// props.loading(true);
// setTimeout(() =>{
//     props.loading(false);
// }, 1000)
props.checkAuth();
}, [pathname]);

return null;
}
